import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

import {COLOR} from '../helper/const';
import Nav from './Nav';
import defaultLogo from '../assets/images/logo.svg';

const HeaderWrapper = styled.header`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
`;

const Logo = styled.img`
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 5px;
  align-self: center;
`;

const Welcome = styled.div`
  position: fixed;
  top: 5px;
  right: 15px;
  width: 170px;
  text-align: left;
  color: ${props => props.theme === 'black' ? COLOR.textColor : '#fff'};
  strong {
    margin-right: 10px;
    color: ${props => props.theme === 'black' ? '#000' : '#fff'};
  }
  span {
    margin-left: 10px;
  }
  p {
    margin-top: 5px;
    line-height: 1.4;
    opacity: .7;
  }
`;

const Header = props => {
  return (
    <HeaderWrapper>
      <Nav theme={props.theme} />
      {
        !props.hideLogo && <Link to="/"><Logo src={defaultLogo} alt="精效数智" /></Link>
      }
      <Welcome theme={props.theme}>
        <h3><strong>体验</strong>|<span>邀约讲解</span></h3>
        <p>如果您对我们很感兴趣，可以前来参观我们的数据化中心，以便获得更多的帮助</p>
      </Welcome>
    </HeaderWrapper>
  )
}

export default Header;